import { extendElementClassWithReactiveElementClass } from "./ReactiveElement";


(function() {

    let config;

    if (document.currentScript) {
        config = {
            useShadow: document.currentScript.getAttribute('useShadow') !== "false" ? true : false
        }
    }
    else {
        config = {
            useShadow: true
        }
    }

    const SPROUT_CONFIG = Object.seal(config);

    // Extend specific HTMLElement classes to enable reactivity
    [
        { class: HTMLDivElement, element: "div" },
        { class: HTMLSpanElement, element: "span" },
        { class: HTMLLabelElement, element: "label" },
        { class: HTMLInputElement, element: "input" },
        { class: HTMLUListElement, element: "ul" }
    ].forEach(itemDefinition => 
        customElements.define(
            `reactive-${itemDefinition.element}`, 
            extendElementClassWithReactiveElementClass(itemDefinition.class, true),
            { extends: itemDefinition.element })
        
    );
})();

