import { COMMANDS as COMMAND_ATTRIBUTES } from "./commands";

const setAttribute = HTMLElement.prototype.setAttribute;

export function extendElementClassWithReactiveElementClass(elementClass, noRender = false) {
    class ReactiveElement extends elementClass {

        #_host
        #templateContent = document.createElement("div")
        #events
        state = {}

        static observedAttributes = Object.keys(COMMAND_ATTRIBUTES).map(command => ('_' + command.toLowerCase()));

        constructor(template=null, events, initialState) {
            super();
            if (events) {
                this.#events = events
            }
            if (initialState) {
                this.initialState = initialState;
            }
            if (template) this.#templateContent = template.content.cloneNode(true);
        }

        #renderTemplate() {
            if (SPROUT_CONFIG.useShadow) {
                (this.attachShadow({ mode: 'open' }))
                .appendChild(this.#templateContent);
            }
            else {
                const fragment = new DocumentFragment();
                fragment.appendChild(this.#templateContent);
                this.appendChild(fragment);
            }
        }

        #bindEvents() {
            if (!this.#events) return;
            const eventRefNames = Object.keys(this.#events);
            const clickActions = {};
            const otherActions = {};
            eventRefNames.forEach(refName=> {
                const value = this.#events[refName];
                if (typeof value === 'function') {
                    clickActions[refName] = value;
                }
                else if (typeof value === 'object') {
                    const eventNames = Object.keys(value);
                    eventNames.forEach(eventName=> {
                        if (eventName === 'click') {
                            clickActions[refName] = value[eventName];
                        }
                        else {
                            if (!otherActions[eventName]) otherActions[eventName] = {};
                            otherActions[eventName][refName] = value[eventName];
                        }
                    });
                }
            });
            const clickRefNames = Object.keys(clickActions);

            function eventHandler(event, eventsObject, refNames) {
                const elementsPath = event.composedPath();
                let target;
                if (elementsPath) {
                    target = elementsPath.find(element => element.hasAttribute && element.hasAttribute('ref') && refNames.includes(element.getAttribute('ref')));
                }
                else {
                    target = event.target.hasAttribute && event.target.hasAttribute('ref') && refNames.includes(event.target.getAttribute('ref')) ? event.target : null;
                }
                if (target) {
                    const ref = target.getAttribute('ref');
                    eventsObject[ref].call(target, event, event.target);
                }
            }

            this.addEventListener('click', (event)=> {
                eventHandler(event, clickActions, clickRefNames);
            }, false);

            const eventNames = Object.keys(otherActions);
            for (const eventName of eventNames) {
                const eventRefNames = Object.keys(otherActions[eventName]);
                this.addEventListener(eventName, (event)=> {
                    eventHandler(event, otherActions, eventRefNames);
                }, false);
            }
        }
        connectedCallback() {
            if (!this.isConnected) {
                console.log ("UNMOUNT");
                return;
            }

            const parent = this.getRootNode().host;
            if (parent) {
                let attrVal;
                INHERITED_ATTRIBUTE_NAMES.forEach(attrName => {
                    attrVal = parent.getAttribute(attrName);
                    if (attrVal !== undefined) {
                        this.setAttribute(attrName, attrVal);
                        if (attrName === '_statepropertyname') {
                            COMMAND_ATTRIBUTES['statepropertyname'].call(this, attrVal);
                        }
                    }
                });

                this.#_host = parent;
            }

            if (this.initialState) {
                for (const [key, value] of Object.entries(this.initalState)) {
                    this.state[key] = value;
                    this.initialSetAttribute(key, '$'+value);
                }
                delete this.initialState;
            }
            for (const attrName of this.getAttributeNames()) {
                const attrValue = this.getAttribute(attrName);
                console.log (`Attribute ${attrName}: ${attrValue}`);
                this.initialSetAttribute(attrName, attrValue);
                if (attrName.indexOf('_') === 0) {
                    const command = attrName.substring(1);
                    COMMAND_ATTRIBUTES[command]?.call(this, attrValue);
                }
            }

            if (!noRender) this.#renderTemplate();

            this.#bindEvents();
        }

        attributeChangedCallback(attributeName, oldValue, newValue) { 
            console.log ("PROP " + attributeName + " changed from " + oldValue + " to " + newValue);
            /*
            if (attributeName.indexOf('_') === 0) {
                const command = attributeName.substring(1);
                COMMAND_ATTRIBUTES[command]?.call(this, newValue);
            }
            */
        }

        getState(stateProp) {
            let thestate;
            let stateVal = this.state?.[stateProp];
            if (stateVal !== undefined && stateVal !== null) thestate = this.state;
            else {
                stateVal = globalThis.state?.[stateProp];
                if (stateVal !== undefined && stateVal !== null) thestate = globalThis.state;
            } 
            console.log ("State val: " + stateVal);
            return [stateVal, thestate];
        }
        
        //static get observedAttributes() { return ["todo-name"]; }
    }

    ReactiveElement.prototype.initialSetAttribute = function (attributeName, attributeValue) {
        console.log (`Setting attribute ${attributeName} to ${attributeValue}`);
        attributeValue = String(attributeValue);
        let valueToSet = attributeValue;
        if (attributeValue.indexOf('$') === 0 && attributeName.indexOf('_') !== 0)  {
            const stateProp = attributeValue.substring(1);
            const [stateVal, thestate] = this.getState(stateProp);
            console.log ("State val: " + stateVal);
            if (stateVal !== undefined && stateVal !== null) valueToSet = stateVal;
            if (!thestate) return;
            // Bind state set to attribute set
            if (!thestate.ATTRIBUTES_statePropToElementsMap) thestate.ATTRIBUTES_statePropToElementsMap = {}
            if (!thestate.ATTRIBUTES_statePropToElementsMap[stateProp]) thestate.ATTRIBUTES_statePropToElementsMap[stateProp] = [];
            thestate.ATTRIBUTES_statePropToElementsMap[stateProp].push({element: this, attributeName});
            if (thestate['$'+stateProp] === undefined) {
                // Prop names with dollar signs are the "private" props in state
                thestate['$'+stateProp] = valueToSet;
                Object.defineProperty(thestate, stateProp, {
                    set(value) {
                        const currentVal = this['$'+stateProp];
                        if (value === currentVal) return;
                        this['$'+stateProp] = value;
                        thestate.ATTRIBUTES_statePropToElementsMap?.[stateProp]?.forEach(({element, attributeName})=> {
                            setAttribute.call(element, attributeName, value)
                            if (attributeName === 'checked') {
                                element.checked = value;
                            }
                            // doSetAttribute.call(element, attributeName, valueToSet)
                        });
                        thestate.TEXT_statePropToElementsMap?.[stateProp]?.forEach(element=> 
                            element.textContent = value
                        );
                    },
                    get() {
                        return this['$'+stateProp];
                    },
                    enumerable: true
                });
            }
        }

        console.log (`Setting ${attributeName} to ${valueToSet}`);
        if (attributeName === 'checked' && valueToSet === false) {
            this.removeAttribute('checked');
        }
        else {
            setAttribute.call(this, attributeName, valueToSet);
        }
    }

    return ReactiveElement;
} 