import { stateToElement, mapStateArrayToElements } from "./state_utils";

const SUPPORTED_INPUT_TYPES_FOR_BINDING = [
    'text', 
    'color', 
    'date', 
    'datetime-local', 
    'email', 
    'month', 
    'number', 
    'password',
    'range',
    'search',
    'tel',
    'time',
    'url',
    'week'
];

export const COMMANDS = {
    map: function(commandValue) {
        console.log ("Mapping: " + commandValue);
        const colon = commandValue.indexOf(':');
        let stateItemsPropertyName = commandValue.substring(0, colon);
        let stateItemsArray = globalThis.state[stateItemsPropertyName];
        if (!stateItemsArray || !Array.isArray(stateItemsArray)) {
            console.warn ("state value for _map is not an array, in state property: " + stateItemsPropertyName);
            return;
        }
        let customElementName = commandValue.substring(colon+1);

        const elements = mapStateArrayToElements(stateItemsArray, customElementName);
        const thiselement = this;
        if (elements.length) thiselement.append(...elements);

        globalThis.state[stateItemsPropertyName] = new Proxy(stateItemsArray, {
            set(arr, index, value) {
                const setResult = Reflect.set(...arguments);
                if (setResult && index !== "length") {
                    const customElement = stateToElement(value, customElementName, `${stateItemsPropertyName}:${index}`);
                    if (customElement) thiselement.appendChild(customElement);
                }
                return setResult;
            },
            deleteProperty(arr, index) {
                const deleteResult = Reflect.deleteProperty(arr, Number(index));
                if (deleteResult) {
                    const elementState = arr[Number(index)];
                    if (elementState.key) document.querySelector(`[key=${elementState.key}]`).remove();
                }
            }
        })
    },
    text: function(commandValue) {
        console.log ("assigning text");
        const stateProp = commandValue;
        const [textValue, thestate] = this.getState(stateProp);
        if (!textValue) return;
        this.textContent = textValue;
        // Bind state set to textContent set
        if (!thestate.TEXT_statePropToElementsMap) thestate.TEXT_statePropToElementsMap = {}
        if (!thestate.TEXT_statePropToElementsMap[stateProp]) thestate.TEXT_statePropToElementsMap[stateProp] = [];
        thestate.TEXT_statePropToElementsMap[stateProp].push(this);
        thestate[stateProp] = textValue;
    },
    bind: function(commandValue) {
        if (this.tagName !== "INPUT" && this.tagName !== "TEXTAREA") return;
        const type = this.getAttribute('type') || 'text';
        const stateProp = commandValue;
        const [stateValue, thestate] = this.getState(stateProp);
        if (stateValue === undefined) return;
        if (SUPPORTED_INPUT_TYPES_FOR_BINDING.includes(type)) {
            this.addEventListener('input', function() {
                thestate[stateProp] = this.value;
            });
        }
        else if (type === "checkbox") {
            this.addEventListener('input', function() {
                thestate[stateProp] = this.checked;
                this.setAttribute('checked', this.checked);
            });
        }
    }
}